/** @jsx jsx */
import { jsx } from "theme-ui";
import { rem } from "@src/theme";

interface ComponentProps {
  type: string;
}

const colourMap = {
  report: "csiBlue",
  insight: "csiYellow",
  infographic: "csiTeal",
  video: "csiRed",
  statistic: "csiPink",
  "academic article": "csiBeige",
  tool: "csiGreen",
};

const TypeTag = ({ type }: ComponentProps) => {
  const typeNormal = type
    ? type.replace(/([A-Z])/g, " $1").replace(/^./, function(str) {
        return str.toUpperCase();
      })
    : "";

  return (
    <div
      sx={{
        display: "inline-block",
        mb: rem(4),
        padding: "2px 8px",
        bg: colourMap[typeNormal.toLowerCase()] || "csiBlue",
        variant: "typography.headingS",
      }}
    >
      {typeNormal}
    </div>
  );
};

export default TypeTag;
